body {
  margin: 0;
}

@media (min-width: 320px) {
  .page-body {
    font-family: "Ubuntu", sans-serif;
    font-weight: 300;
    background-image: url(../../img/mobile-intro-bg.jpg);
    background-repeat: no-repeat;
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 320px;
    margin: 0 auto;
  }
}
@media (min-width: 320px) {
  .page-header {
    display: flex;
    width: 280px;
    justify-content: space-between;
    margin-top: 20px;
  }

  .page-header__logo {
    width: 28px;
  }

  .page-header__button {
    width: 108px;
    height: 34px;
  }

  .page-header__nav-toggle {
    font-family: "Ubuntu", sans-serif;
    font-weight: 600;
    color: #FFFFFF;
    cursor: pointer;
    border: none;
    background-color: transparent;
    text-transform: uppercase;
    position: relative;
    width: 24px;
    background-color: #FFFFFF;
    height: 2px;
    box-shadow: 0 8px 0 #FFFFFF;
  }
  .page-header__nav-toggle::after {
    content: "";
    position: absolute;
    width: 16px;
    height: 2px;
    background-color: #FFFFFF;
    top: 16px;
    left: 8px;
  }
}
@media (min-width: 320px) {
  .logo {
    background-image: url("../../img/digpl-logo.svg");
    background-repeat: no-repeat;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@media (min-width: 320px) {
  .order-button {
    font-family: "Ubuntu", sans-serif;
    font-weight: 600;
    color: #FFFFFF;
    cursor: pointer;
    border: none;
    background-color: transparent;
    text-transform: uppercase;
    outline: 2px solid #0DD1A2;
    border-radius: 2px;
    font-size: 14px;
  }
}
@media (min-width: 320px) {
  .nav--closed {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
  }
}
@media (min-width: 320px) {
  .nav-list {
    list-style-type: none;
  }
}
@media (min-width: 320px) {
  .page-main {
    width: 288px;
    margin: 0 auto;
  }
}
@media (min-width: 320px) {
  .intro {
    margin-top: 90px;
  }

  .intro__title {
    margin: 0;
    font-size: 36px;
    font-weight: 400;
    color: #FFFFFF;
    text-align: center;
  }

  .intro__description {
    margin: 0;
    font-size: 15px;
    font-weight: 300;
    line-height: 21px;
    color: #FFFFFF;
    text-align: center;
  }
}